.description {
  padding: 0 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__title {
    margin-bottom: 50px;
  }
  &__text {
    margin-bottom: 40px;
  }
  &__img {
    position: relative;
  }
  &__item-wrapper {
    position: relative;
    display: flex;
    align-items: center;
  }
  &__item-wrapper::after {
    content: url("./images/description/layer1.png");
    position: absolute;
    top: -20px;
    left: -20px;
  }
  &__item-wrapper::before {
    content: "";
    width: 60px;
    height: 2px;
    background-color: rgb(165, 160, 160);
    position: absolute;
    bottom: 0;
  }
  &__logo-wrapper {
    margin-bottom: 30px;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 75px;
  }
  &__item {
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &-wrapper {
      display: flex;
      padding-bottom: 30px;
      margin-bottom: 20px;
    }
    &-title {
      margin: 20px 0 0 20px;
    }
  }
}

.description__statistics {
  position: relative;
  &-logo {
    background-color: white;
    position: absolute;
    padding: 20px 30px;
    left: calc(50% - 45px);
    top: -40px;
    z-index: 2;
  }

}

.description__statistics-text {
  padding: 40px;
  border: 2px solid rgb(239,214,197);
  border-radius: 5px;
}

@media screen and (max-width: 900px) {
  .description__container {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
  }
  .description__item {
      margin-bottom: 30px;
      width: 100%;
  }
  
}