@import "./section.scss";
@import "./general.scss";

.header {
  width: 100%;
  margin-bottom: 50px;
  background: url("./images/header/2.png") no-repeat center top;
  background-size: contain;

  display: flex;
  justify-content: center;
  position: relative;

  &__logo-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    width: 200px;
    height: 60px;
  }

  &__logo-name {
    margin: 0 0 0 10px;
  }

  &__left {
    height: 35vw;
    max-height: 800px;
    width: 50%;
    max-width: 700px;
    min-height: 600px;

    display: flex;
    flex-direction: column;
    align-items: flex-end;

    &-top {
      display: flex;
      justify-content: flex-end;
      position: relative;
      width: 50%;
      height: 75%;
    }

    &-down {
      position: relative;
      width: 50%;
      height: 25%;

      display: flex;
      flex-direction: column;
      align-items: flex-end;
      justify-content: flex-end;
    }

  }
  &__right {
    width: 50%;
    max-width: 700px;
    padding-left: 40px;

    &-top {
      display: flex;
      justify-content: space-between;
      margin-bottom: 55px;
    }

    &-down {
      display: flex;
      justify-content: space-between;
    }
  }

  &__form {
    position: relative;
    min-width: 250px;
    width: 40%;
    padding: 10px;

    background-image: url("./images/header/rectangle-green.png");
    background-size: cover;

    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;

    &::before {
      content: "Бесплатнo";
      position: absolute;
      left: 30%;
      padding-left: 40px;

      background: url("./images/header/car.png") no-repeat;
      color: rgb(139, 133, 133);
      
      &:hover {
        transform: translateX(50px);
      }
    }
  }
}

.medicine-img {
  width: 100%;
  max-width: 900px;
  position: relative;
  top: -15%;
  left: 50px;
  opacity: 0.9;
  animation: timer 5s infinite linear alternate;
  transform-origin: top;
}

.legs-img {
  max-width: 900px;
  width: 100%;
}

.main-form {
  width: 100%;
  height: 550px;
  background-size: cover;
  background-image: url("./images/header/rectangle-white.png");
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  box-sizing: border-box;
  text-align: center;
  padding: 50px 30px 0;
}

.form {
  &__input {
    position: relative;
    height: 60px;
    border-radius: 25px;
    width: 100%;
    background-color: #edf2f7;
    border: none;
    outline: none;
    margin-bottom: 20px;
    padding: 20px 40px;
    box-sizing: border-box;
    transition: background-color 0.7s;
    &:focus {
      background-color: #d7dee6;
    }
    &-tel {
      background-image: url("./images/header/tel.png");
      background-repeat: no-repeat;
      background-position: 7% 50% ;
    }
    &-name {
      background-image: url("./images/header/person.png");
      background-repeat: no-repeat;
      background-position: 7% 50% ;
    }
  }
  &__text-security {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
  &__button {
    position: relative;
    height: 70px;
    border-radius: 25px;
    width: 100%;
    border: none;
    outline: none;
    margin-bottom: 20px;
    padding: 5px;
    box-sizing: border-box;
    transition: opacity 0.7s, transform 0.7s;
    &-text {
      display: inline-block;
      border-radius: 25px;
      border: 1px solid yellow;
      width: 97%;
      height: 97%;
      padding: 6px 0;
      line-height: 20px;
      box-sizing: border-box;
    }
    &:hover {
      cursor: pointer;
      transform: scale(1.03);
      opacity: 0.7;
    }
  }
}

.priority {
  display: flex;
  width: 31%;
  &-text {
    font-style: italic;
  }
  &-img {
    width: 40px;
    height: 40px;
    margin-right: 15px;
  }

}

.pros {
  width: 50%;
  &__item {
    display: flex;
    align-items: center;
  }
  &-icon {
    box-sizing: border-box;
    margin: 30px 25px 30px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: #a3c83d;
    border: 5px solid rgb(228, 241, 198);
    transition: transform 0.6s;
    position: relative;

    &:hover {
      transform: scale(1.1);
    }
  }
  &__text {
    width: 70%;
  }
}

.icon-center::after {
  content: url("./images/header/arrow.png");
  position: absolute;
  left: calc(50% - 5px);
  top: -45px;
}

.prices {
  display: flex;
  justify-content: space-between;
  margin: 30px 0;
}

.price {
  padding: 0 10px;
  border-radius: 5px;
}

.price-new {
  animation: color-pr 1s infinite linear alternate;
  transform-origin: top;
}

.price-old {
  position: relative;
  &::after {
    content: url("./images/header/cross.png");
    position: absolute;
    left: 0;
  }
}

main {
  position: relative;
  top: -20px;
  width: 100%;
  height: 100px;
  background-image: url("./images/header/sloi_9.png");
}

@keyframes timer {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.1);
  }
}

@keyframes color-pr  {
  from {
    background-color: #fdc901;
  }
  to {
    background-color: rgb(255, 153, 0);
  }
}

@media screen and (min-width: 2300px) {
  h1 {
    font-size: 35px;
  }

  h3 {
    font-size: 28px;
  }
}

@media screen and (max-width: 1900px) {
  h1 {
    font-size: 25px;
  }

  h3 {
    font-size: 18px;
  }

  .pros {
    width: 30%;
  }
  .header {
    // max-height: 1200px;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    background-size: contain;

    &__left {
      min-height: 300px;
      margin-bottom: 50px;
      height: 50%;
      justify-content: flex-start;
      &-down {
        height: 30%;
      }
      &-top {
        height: 70%;
      }
    }

    &__right {
      width: 90%;
      padding: 0;
      height: 50%;
      &-down {
        justify-content: space-evenly;
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .header {
    &__right-down {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &-form {
      width: 90%;
    }
  }
  .pros {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__item {
      justify-content: space-between;
    }
  }

}


@media screen and (max-width: 800px) {
  .header {
    &__left {
      height: 200px;
    }
  }
  .pros {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__item {
      justify-content: space-between;
    }
  }

}